.about_content p {
    margin: 1rem;
    color: var(--color-white);
    text-align: justify;
    margin-bottom: 50px !important; 
    text-indent: 5%;
}
.about_content h1{
    color:rgb(153, 248, 235) ;
    text-align: center;
}

.about_content h3{
    font-size: 25px;
    text-align: center;
}

.btn-1{
    text-align: center !important;
    place-items: center;
}


/* ===================== Media Queries (Medium Device) ====================== */
@media screen and (max-width: 3000px) {
    .about_container{
        grid-template-columns: 1fr;
        gap: 0;
    }
    .about_me{
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about_content{
        text-align: center;
    }
    .about_content p {
        margin: 1rem 0 1.5rem;
    }
}
 

/* ===================== Media Queries (Small Device) ====================== */
@media screen and (max-width: 600px) {
    .about_me {
        width: 65%;
        margin: 0 auto 0rem;
    }
    .about_card{
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }
    .about_content {
        text-align: center;
    }
    .about_content p{
        font-size: 13px;
        margin: 1.5rem 0;
    }
    .about_content h1{
        font-size: 25px;
        text-align: center;
    }
    
    .about_content h3{
        font-size: 15px;
        text-align: center;
    }
}