@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');
/* @import url("text.css"); */

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;    
}

:root {
    --color-bg: #1f1f38;
    --color-bg-varient: #2c2c6c;
    --color-primary: #adc0cd;
    --color-primary-verient: rgba(77, 181, 255, 0.4);
    --color-white: #fff ;
    --color-light: rgba(255, 255, 255, 0.6);
    --color-my: rgba(151, 156, 157, 0.3);
    --color-myphoto: #bec1c6;
    --color-yellow: #221123;
    --color-p: #66757d;
    --color-red: rgb(255, 0, 0);
    --color-bg-1: rgb(0, 221, 255);
    --color-bg-2: rgb(59, 186, 206);

    --transition: all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;

}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}
/* =========== Header Top ============= */
body {
    font-family: 'Poppins', sans-serif;
    background: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
    background-image: url(../src/assets/bg-texture.png);
    /* background-image: url(../src/text.css); */

    padding-top: 3rem;
}

/* ================== MY STYLE ================== */
.container {
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1, 
h2, 
h3, 
h4, 
h5,{
    font-weight: 300;
}

h1{
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
    /* height: 100vh; */
}

section > h2,
section > h5 {
    text-align: center;
    color: var(--color-light);
}
section > h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
}
/* ====================== Change Colour Scroll and Social ==================== */
.text-light {
    color: var(--color-light);
}

a {
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover {
    color: var(--color-white);
}
/* ====================== Change Colour Scroll and Social (old) ==================== */


.btn {
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 30px;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition); 
}

.btn::after, .btn::before {
    content: "";
    position: absolute;
    opacity: 0.3;
    /* background: #000; */
    border-radius: inherit;
    width: 100%;
    height: 100%;
    left: 0;
    bottom: 0;
    z-index: -1;
    transition: transform 0.3 ease;
}
.btn:hover{
    transform: translate(-12px, -12px);
}
.btn:hover{
    transform: translate(6px, 6px);
}
.btn:hover{
    transform: translate(-12px, 12px);
}


/* ======= Button ========*/
.btn-1 {
    background: rgb(253, 219, 1);
    color: rgb(0, 0, 0);
    border-radius: 30px;
    transition: transform 0.3 ease;
}
.btn-1::after, .btn-1::before {
    content: "";
    position: absolute;
    opacity: 0.3;
    /* background: #000; */
    border-radius: inherit;
    width: 100%;
    height: 100%;
    left: 0;
    bottom: 0;
    z-index: -1;
    transition: transform 0.3 ease;
}
.btn-1:hover{
    transform: translate(-12px, -12px);
}
.btn-1:hover{
    transform: translate(6px, 6px);
}
.btn-1:hover{
    transform: translate(12px, 12px);
}




/* ================ Change button Collor ================ */
.btn-light{
    padding: 12px 32px;
    font-size: 1rem;
    text-transform: uppercase;
    /* background: transparent; */
    background: #adc0cd;

    color: rgb(15, 12, 14);
    border: 1px solid rgb(255, 255, 255);
    font-weight: 600;
    cursor: pointer;
}

.btn-pi{
    padding: 12px 32px;
    font-size: 1rem;
    text-transform: uppercase;
    /* background: transparent; */
    background: #ffff00;

    color: rgb(47, 5, 238);
    border: 1px solid rgb(255, 255, 255);
    font-weight: 600;
    cursor: pointer;
}
/* ================ Change button Collor ================ */


.btn :hover{
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
    box-shadow: rgb(143, 140, 140);
}

.btn:primery {
    background: var(--color-primary);
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

/* ===================== Media Queries (Medium Device) ====================== */
@media screen and (max-width: 3000px) {
    .container {
        width: var(--container-width-md);
    }

    section {
        margin-top: 6rem;
    }
}

/* ===================== Media Queries (Small Device) ====================== */
@media screen and (max-width: 600px) {
    .container {
        width: var(--container-width-sm);
    }

    section > h2 {
        margin-bottom: 2rem;
    }
    h1{
        font-size: 2rem;
        color: rgb(153, 248, 235);
    }
}